.stars {
  display: flex;

  @for $starCount from 1 through 5 {
    &--#{$starCount} .stars__star:nth-child(-n + #{$starCount}) {
      background-image: url(../images/star-active.svg);
    }
  }

  &__star {
    @include size-box($star-size);
    background: url(../images/star.svg) no-repeat center;
    margin-right: 4px;
  }
}
